import { connect } from 'react-redux';
import React, { PureComponent } from 'react';
import * as actionCreators from './store/actionCreators'
import axios from 'axios';
import HeaderUI from './indexUI';

class Header extends PureComponent {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <HeaderUI 
        marquee={this.props.goodSentences} 
        navList={this.props.navList} 
        focused={this.props.focused} 
        handleNav={() => (this.props.handleNav)}
      />
    )
  }

  componentDidMount() {
    this.props.initNavList();
  }
  
}

const mapStateToProps = (state) => {
  return {
    navList: state.header.get('navList'),
    goodSentences: state.header.get('goodSentences'),
    focused: state.header.get('focused')
  }
}

const mapDispathToProps = (dispatch) => {
  return {
    initNavList() {
      axios.get('http://localhost:3002/nav_list')
      .then(res => {
        const data = res.data.result;
        dispatch(actionCreators.initNavListAction(data));
      })
    },
    handleNav(index) {
      dispatch(actionCreators.selectNavItemAction(index));
    }
  }
}

export default connect(mapStateToProps,mapDispathToProps)(Header);

